body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "PT Sans Narrow", sans-serif;
  font-size: 20px;
  line-height: 27px;
  font-weight: 400;
  font-display: swap;
  color: #ffffff;
  background-color: #292929;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: insert(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
img {
  max-width: 100%;
  height: auto;
}
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}
.link-reset {
  text-decoration: none;
  color: #ffffff;
}
.title-style {
  margin: 0;
  padding: 0;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
}
.body--form {
  position: relative;
}
.button {
  text-decoration: none;
  color: #ffffff;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;
  color: #000000;
  background: none;
  border: none;
}
.button--submit {
  text-align: center;
  color: #ffffff;
  background-color: #826550;
}
.button--ghost:hover,
.button--ghost:focus {
  border-color: #d5c9bd;
}
.button--ghost:hover {
  cursor: pointer;
}
.button-radio + label:hover::before {
  border-color: #d5c9bd;
}
.button-radio + label:active::before {
  background-color: #d5c9bd;
}
.button-radio:checked + label:hover::before,
.button-radio:checked + label:hover::after {
  border-color: #d5c9bd;
}
.button-radio:checked + label:active::before,
.button-radio:checked + label:active::after {
  background-color: #d5c9bd;
}
.button-checkbox + label:hover::before {
  border-color: #d5c9bd;
}
.button-checkbox + label:active::before {
  background-color: #d5c9bd;
}
.button--brown:hover {
  background-color: #6a4d38;
  cursor: pointer;
}
.button--brown:active span {
  opacity: 0.3;
}
.button--brown:disabled {
  background-color: #aeaeae;
}
.input:hover {
  border-color: #d5c9bd;
}
.input:focus {
  outline: none;
  border-color: #999999;
}
.page-header {
  display: flex;
  flex-direction: column;
}
.page-header__logo {
  order: 1;
  height: 85px;
  margin: 0 auto;
  text-align: center;
}
.page-header__logo img {
