.prices {
  position: relative;

  width: auto;
  padding-top: 70px;
  padding-bottom: 20px;

  background-color: @bg-grey;

  &::after {
    content: "";

    position: absolute;
    bottom: -34px;
    left: 0;

    width: 100%;
    height: 34px;

    background: linear-gradient(to bottom left, @bg-grey 49%, transparent 51%)
        left / 50.5% 100% no-repeat,
      linear-gradient(to bottom right, @bg-grey 49%, transparent 51%) right /
        50.5% 100% no-repeat;
  }
}

.prices__title {
  .title-style;
  text-align: center;
  color: #ffffff;
}

.prices__wrapper {
  width: 88%;
  max-width: 960px;
  margin: 0 auto;
}

.prices__table {
  margin: 0 auto;
  margin-top: 30px;
}

.prices__table,
.prices__table td,
.prices__table tr {
  border-collapse: collapse;
}

.prices__table tr {
  display: flex;
  flex-wrap: wrap;
}

.prices__number {
  display: none;
}

.prices__name {
  width: 72%;
  font-weight: 700;
  color: #ffffff;
}

.prices__additionally {
  width: 100%;
  margin-bottom: 16px;

  order: 3;
}

.prices__value {
  width: 26%;
}

@media (min-width: @tablet-width) {
  .prices {
    padding-top: 90px;
    padding-bottom: 56px;

    &::after {
      content: none;
    }
  }

  .prices__wrapper {
    width: 84%;
    max-width: 960px;
  }

  .prices__title {
    font-size: 40px;
    text-align: left;
  }

  .prices__table {
    width: 100%;
    margin-top: 53px;
  }

  .prices__table tr {
    height: 54px;
    display: table-row;
  }

  .prices__table td {
    padding: 0 20px;

    border: 1px solid #484848;
  }

  .prices__number {
    width: 11%;
    display: table-cell;

    text-align: center;
    color: rgba(255, 255, 255, 0.3);
  }

  .prices__name {
    width: 39%;
  }

  .prices__additionally {
    width: 25%;
    margin-bottom: 0;
    order: 0;
  }

  .prices__bracket {
    display: none;
  }

  .prices__value {
    width: 25%;
  }
}

@media (min-width: @desktop-width) {
  .prices {
    width: auto;
    padding-top: 83px;
    padding-bottom: 83px;
  }

  .prices__wrapper {
    width: 960px;
    display: flex;
    flex-wrap: wrap;
  }

  .prices__title {
    width: 100%;
    margin-bottom: 54px;
  }

  .prices__table {
    width: 630px;
    margin: 0;
  }

  .prices__table td {
    padding: 0 18px;
  }
}
